import { makeStyles } from '@mui/styles'
import React from 'react'

import Button from '@mui/material/Button';
import { fontSize, width } from '@mui/system';

const useStyles = makeStyles((theme)=>({
    mainContainer:{
        margin: "20px auto",
        width: "100%",
        maxWidth: "1330px",

    },
    boxes:{
        margin: "0 -20px 0 20px",
        // margin: "0 20px",
        direction: "ltr",
        display: "grid",
        gridGap: "10px",
        gredTemplateColumns: "50px 50px 50px 50px",
        gridTemplateRows: "auto",
        gridTemplateAreas: `
        "One   One  one  Two  Five"
        "Three Four Four Four Five"
        `,
    },
    itemOne:{
        position: "relative",
        // zIndex: "-2",
        height: "220px",
        overflow: "hidden",
        direction: "ltr",
        display: "flex",
        borderRadius: "8px",
        gridArea: "One",
        background: theme.palette.primary.siteColorLinearOne,
        gridColumnStart: "1",
        gridColumnEnd: '4',
    },
    itemTwo:{
        position: "relative",
        // zIndex: "-2",
        height: "220px",
        overflow: "hidden",
        direction: "ltr",
        display: "flex",
        borderRadius: "8px",
        gridArea: "Two",
        background: theme.palette.primary.siteColorLinearTwo,
    },
    itemThree:{
        position: "relative",
        // zIndex: "-2",
        height: "220px",
        overflow: "hidden",
        direction: "ltr",
        display: "flex",
        borderRadius: "8px",
        gridArea: "Three",
        background: theme.palette.primary.siteColorLinearThree,
    },
    itemFour:{
        position: "relative",
        // zIndex: "-2",
        height: "220px",
        overflow: "hidden",
        direction: "ltr",
        display: "flex",
        gridArea: "One",
        borderRadius: "8px",
        gridArea: "Four",
        background: theme.palette.primary.siteColorLinearFour,
    },
    itemFive:{
        direction: "ltr",
        display: "flex",
        gridArea: "Five",
        background: "orange",
        overflow: "hidden",
        width: "250px",
        height: "100%",
        borderRadius: "8px",
        height: "455px",
    },
    itemFiveImgae:{
        // background: "url('/images/demoimg1.jpg')",
        objectFit: "cover",
        height: "100%",
        width: "100%",
        display: "flex",
        justifyContent: "center",
        overflow: "hidden",
        '& img':{
            height: "455px",
        }
    },
    itemImage:{
        width: "40%",
        '& img':{
            height: "400px",
            marginTop: "-85px",
             marginLeft: "-190px",
             marginRight: "-35px"
        }
    },
    itemImageTwo:{
        width: "60%",
        '& img':{
            height: "270px",
            marginTop: "-5px",
            marginLeft: "-190px",
            marginRight: "-10px"
        }
    },
    itemDetails:{
        width: "60%",
        display: "flex",
        flexDirection: "column",
        padding: "20px 20px 20px 0",
        color: "#fff",
        justifyContent: "space-between"
    },
    itemTitle:{
        '& h4':{
            fontSize: "20px",
        },
        '& h5':{
            background: "rgba()",
            marginTop: "20px",
            fontSize: "12px",
            fontWeight: "normal"
        },
    },
    itemBtn:{
        background: "#fff!important",
        width: "136px"
    },
    betwinItemTitle:{
        background: theme.palette.btnOne.main,
        height: "2px",
        width: "50%",
        margin: "10px 0",
        mixBlendMode: "screen",
    },
    betwinItemTitleTwo:{
        background: theme.palette.btnTwo.main,
        height: "2px",
        width: "50%",
        margin: "10px 0",
        mixBlendMode: "screen",
    },
    betwinItemTitleThree:{
        background: theme.palette.btnThree.main,
        height: "2px",
        width: "50%",
        margin: "10px 0",
        mixBlendMode: "screen",
    },
    betwinItemTitleFour:{
        background: theme.palette.btnFour.main,
        height: "2px",
        width: "50%",
        margin: "10px 0",
        mixBlendMode: "screen",
    },
    itemBackgroundShadow:{
        zIndex: "-1",
        position: "absolute",
        background: theme.palette.btnOne.main,
        width: "400px",
        height: "400px",
        marginTop: "-90px",
        marginLeft: "-200px",
        borderRadius: "50%",
        mixBlendMode: "hard-light",
    },
    itemBackgroundShadowTwo:{
        zIndex: "-1",
        position: "absolute",
        background: theme.palette.btnTwo.main,
        width: "400px",
        height: "400px",
        marginTop: "-90px",
        marginLeft: "-230px",
        borderRadius: "50%",
        mixBlendMode: "hard-light",
    },
    itemBackgroundShadowThree:{
        zIndex: "-1",
        position: "absolute",
        background: theme.palette.btnThree.main,
        width: "400px",
        height: "400px",
        marginTop: "-90px",
        marginLeft: "-230px",
        borderRadius: "50%",
        mixBlendMode: "hard-light",
    },
    itemBackgroundShadowFour:{
        zIndex: "-1",
        position: "absolute",
        background: theme.palette.btnFour.main,
        width: "400px",
        height: "400px",
        marginTop: "-90px",
        marginLeft: "-200px",
        borderRadius: "50%",
        mixBlendMode: "hard-light",
    },
    
    //  @media
    
    [theme.breakpoints.down("md")]:{
        boxes:{
            margin: "0 20px",
            direction: "ltr",
            display: "grid",
            gridGap: "10px",
            gredTemplateColumns: "50px 50px 50px 50px",
            gridTemplateRows: "auto",
            gridTemplateAreas: `
            "One   One  one  Two "
            "Three Four Four Four"
            `,
        },
        itemFive:{
            display: "none"
        },

        itemBtn:{
            background: "#fff!important",
            width: "90px"
        },
        itemImage:{
            width: "40%",
            '& img':{
                height: "400px",
                marginTop: "-85px",
                 marginLeft: "-290px",
                }
            },
            itemImageTwo:{
                width: "60%",
                '& img':{
                    height: "200px",
                    marginTop: "35px",
                    marginLeft: "-190px",
            }
        },
        itemTitle:{
            '& h4':{
                fontSize: "16px",
            },
            '& h5':{
                marginTop: "8px",
                fontSize: "12px",
                fontWeight: "normal",
                maxWidth: "200px"
            },
        },
    },


    [theme.breakpoints.down("sm")]:{
        boxes:{
            margin: "0 10px",
            direction: "ltr",
            display: "grid",
            gridGap: "10px",
            gredTemplateColumns: "50px 50px 50px 50px",
            gridTemplateRows: "auto",
            gridTemplateAreas: `
            "One   One  one  Two "
            "Three Four Four Four"
            `,
        },
        itemFive:{
            display: "none"
        },

        itemBtn:{
            background: "#fff!important",
            width: "50px",
            maxHeight: "20px",
            fontSize: "10px!important"
        },
        itemImage:{
            width: "40%",
            '& img':{
                height: "200px",
                marginTop: "5px",
                 marginLeft: "-290px",
                 marginRight: "-35px"
                }
            },
            itemImageTwo:{
                width: "60%",
                '& img':{
                    height: "170px",
                    marginTop: "15px",
                    marginLeft: "-190px",
                    marginRight: "-30px"
            }
        },
        itemTitle:{
            '& h4':{
                fontSize: "12px",
            },
            '& h5':{
                marginTop: "5px",
                fontSize: "9px",
                fontWeight: "normal",
                maxWidth: "200px"
            },
        },
        itemDetails:{
            width: "60%",
            display: "flex",
            flexDirection: "column",
            padding: "10px 10px 10px 0",
            color: "#fff",
            justifyContent: "space-between"
        },
        itemOne:{
            height: "170px",
            paddingLeft:"30px",
            borderRadius: "6px",
        },
        itemTwo:{
            height: "170px",
            borderRadius: "6px",
        },
        itemThree:{
            height: "170px",
            borderRadius: "6px",
        },
        itemFour:{
            height: "170px",
            paddingLeft:"0px",
            borderRadius: "6px",
        },
    },
    // [theme.breakpoints.up('md')]:{
    //     boxes: {
    //         margin: "0 -20px 0 20px",
    //     }
    // }
}))

function GroupBaners() {
    const classes = useStyles();
    return (
        <header className={classes.mainContainer}>
            <div className={classes.boxes}>
                {/* ---------- itemONe---------- */}
                <div className={classes.itemOne}>
                    <div className={classes.itemBackgroundShadow}></div>
                    <div className={classes.itemImage}>
                        <img src="/novinMobl/images/product-images/png/01.png" alt=""/>
                    </div>
                    <div className={classes.itemDetails}>
                        <div className={classes.itemTitle}>
                            <h4>انواع مبل</h4>
                            <div className={classes.betwinItemTitle}></div>
                            <h5>
                                  مبل راحتی ،  مبلمان کلاسیک ،  مبل تختخواب شو ،  مبل ال ،  مبلمان استیل
                            </h5>
                        </div>
                        <Button href="#" color="btnOne" variant='text' size="small" className={classes.itemBtn}>
                            <h4>بیشتر</h4>
                        </Button>
                    </div>
                </div>
                {/* ------------ item tow ---------- */}
                <div className={classes.itemTwo}>
                    <div className={classes.itemBackgroundShadowTwo}></div>
                    <div className={classes.itemImageTwo}>
                        <img src="/novinMobl/images/product-images/png/07.png" alt=""/>
                    </div>
                    <div className={classes.itemDetails}>
                        <div className={classes.itemTitle}>
                            <h4>انواع صندلی</h4>
                            <div className={classes.betwinItemTitleTwo}></div>
                            <h5>
                                  مبل راحتی ،  مبلمان کلاسیک ،  مبل تختخواب شو ،  مبل ال ،  مبلمان استیل
                            </h5>
                        </div>
                        <Button color="btnTwo" variant='text' size="small" className={classes.itemBtn}>
                            <h4>بیشتر</h4>
                        </Button>
                    </div>
                </div>
                {/*  ---------- item three --------- */}
                <div className={classes.itemThree}>
                    <div className={classes.itemBackgroundShadowThree}></div>
                    <div className={classes.itemImageTwo}>
                        <img src="/novinMobl/images/product-images/png/05.png" alt=""/>
                    </div>
                    <div className={classes.itemDetails}>
                        <div className={classes.itemTitle}>
                            <h4>کتابخانه و کمد</h4>
                            <div className={classes.betwinItemTitleThree}></div>
                            <h5>
                                  مبل راحتی ،  مبلمان کلاسیک ،  مبل تختخواب شو ،  مبل ال ،  مبلمان استیل
                            </h5>
                        </div>
                        <Button color="btnThree" variant='text' size="small" className={classes.itemBtn}>
                            <h4>بیشتر</h4>
                        </Button>
                    </div>
                </div>
                {/*  ------------ item four ----------*/}
                <div className={classes.itemFour}>
                    <div className={classes.itemBackgroundShadowFour}></div>
                    <div className={classes.itemImage}>
                        <img src="/novinMobl/images/product-images/png/03.png" alt=""/>
                    </div>
                    <div className={classes.itemDetails}>
                        <div className={classes.itemTitle}>
                            <h4>مبلمان اداری</h4>
                            <div className={classes.betwinItemTitleFour}></div>
                            <h5>
                                  مبل راحتی ،  مبلمان کلاسیک ،  مبل تختخواب شو ،  مبل ال ،  مبلمان استیل
                            </h5>
                        </div>
                        <Button color="btnFour" variant='text' size="small" className={classes.itemBtn}>
                            <h4>بیشتر</h4>
                        </Button>
                    </div>
                </div>
                {/*  ------------ item five ----------*/}
                <div className={classes.itemFive}>
                    <div className={classes.itemFiveImgae}>
                        <img src="/novinMobl/images/demoimg1.jpg" alt=""/>
                    </div>
                </div>
            </div>
        </header>
    )
}

export default GroupBaners
