import * as React from 'react';
import {Tabs,Tab} from '@mui/material';
import { makeStyles } from '@mui/styles';


import { Link } from 'react-router-dom';

const useStyles = makeStyles((theme)=>({
    mainTabs:{
        // direction: "ltr!important",
    }
}))



export default function NavBarTabs() {
  const classes = useStyles();

  // tabs
  const [value, setValue] = React.useState(2);
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Tabs className={classes.mainTabs} textColor="secondary" indicatorColor="secondary" centered value={value} onChange={handleChange} aria-label="disabled tabs example">
      <Tab value={8} component={Link} to="/novinMobl/category" label="کتابخانه و کمد" />
      <Tab value={7} component={Link} to="/novinMobl/category" label="میز" />
      <Tab value={6} component={Link} to="/novinMobl/category" label="صندلی" />
      <Tab value={5} component={Link} to="/novinMobl/category" label="مبل" />
      <Tab value={4} component={Link} to="/novinMobl/category" label="مبلمان منزل" />
      <Tab value={3} component={Link} to="/novinMobl/category" label="مبلمان اداری"  />
      <Tab value={2} component={Link} to="/novinMobl" label="خانه" />
    </Tabs>
  );
}