import { makeStyles } from '@mui/styles'
import React from 'react'

import { Link } from 'react-router-dom';

// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

import 'swiper/swiper.min.css';
import 'swiper/swiper-bundle.css';

// import Swiper core and required modules
import SwiperCore, {
    Autoplay,Navigation,Pagination
  } from 'swiper';
import { Button } from '@mui/material';

// install Swiper modules
SwiperCore.use([Autoplay,Navigation,Pagination]);



const useStyles = makeStyles((theme)=>({
    mainContainer:{
        padding: "5px 0 35px 0",
        marginBottom: "20px",
        background: theme.palette.primary.siteColorLinearTwo
    },
    titleContainer:{
        maxWidth: "1300px",
        margin: "0 auto"
    },
    sliderTitle:{
        color: "white",
        margin: "10px 20px  20px 0",
    },
    afterTitle:{
        background: "red",
        height: "3px",
        width: "15%",
        margin: "5px 0",
        background: theme.palette.btnTwo.main,
        mixBlendMode: "color-dodge",
    },
    slider:{
        padding: "0 20px",
        margin: "0 auto",
        maxWidth: "1300px",
        '& .swiper-pagination':{
            top: "220px"
        },
    },
    slide:{
    },
    product:{
        background: "#fff",
        height: "240px",
        borderRadius: "5px",
        display: "flex",
        flexDirection: "column",
    },
    productImage:{
        height: "130px",
        display: "flex",
        justifyContent: "center",
        margin: "5px",
        padding: "5px",
        "& img":{
            height: "130px",

        }
    },
    productDetails:{
        margin:"0 8px",
    },
    productTitle:{
        fontSize: "15px",
        '& h5':{
            margin: "0 8px"
        }
    },
    productSubtitle:{
        marginTop: "8px",
        fontSize: "14px",
        '& h5':{
            margin: "0 8px"
        }
    },
    productPriceContainer:{
        display: "flex",
        alignItems: "flex-end",
        margin: "8px",
        justifyContent: "space-between",

    },
    productPrice:{
        fontSize: "16px",
    },
    productPriceAfter:{
        marginTop: "3px",
        paddingRight: "5px",
        '& h5':{
            color: "white",
            fontWeight: "normal",
            background: theme.palette.btnTwo.main,
            fontSize: "11px",
            padding: "3px",
            borderRadius: "5px"

        }

    },
    addToBasketBtn:{
        color: "#fff!important",
        fontSize: "14px!important",
        maxWidth: "5px!important",
        padding: "2px!important",
        minWidth: "40px!important"
    },
    priceContainer:{
        display:"flex",
        alignItems: "center"
    },

    [theme.breakpoints.down("sm")]:{
        addToBasketBtn:{
            fontSize: "12px!important",
            maxWidth: "5px!important",
            padding: "2px!important",
            minWidth: "30px!important"
        },
        productPrice:{
            "& h5":{
                fontSize: "12px",
            }
        },
        productDetails:{
            margin: "0 2px",
        },
        productSubtitle:{
            '& h5':{
                margin: "0 8px"
            }

        },
        productTitle:{
            '& h5':{
                margin: "0 8px"
            }
        }
    }


}))

function ProductLIstOne() {
    const classes = useStyles();


    return (
        <div className={classes.mainContainer}>
            <div className={classes.titleContainer}>
                <div className={classes.sliderTitle}>
                    <h4>آخرین محصولات</h4>
                    <div className={classes.afterTitle}></div>
                </div>
            </div>
            {/* -------------- Slider ------------- */}
            <Swiper className={classes.slider} slidesPerView={2} spaceBetween={10} breakpoints={{
            "640": {
                "slidesPerView": 2,
                "spaceBetween": 10
            },
            "768": {
                "slidesPerView": 4,
                "spaceBetween": 15
            },
            "1024": {
                "slidesPerView": 5,
                "spaceBetween": 20
            }
            }}>
                {/*---------- Slide -------- */}
                <SwiperSlide className={classes.slide}>
                    <div className={classes.product}>
                        <Link to="/novinMobl/product" className={classes.productImage}>
                            <img src="/novinMobl/images/product-images/novinMobl-img (32).jpg" />
                        </Link>
                        <div className={classes.productDetails}>
                            <div className={classes.productTitle}>
                                <h5>صندلی اداری چرمی</h5>
                            </div>
                            <div className={classes.productSubtitle}>
                                <h5>مدل G-1</h5>
                            </div>
                            <div className={classes.productPriceContainer}>
                                <div className={classes.addToBasket}>
                                    <Button variant="contained" color="btnThree" className={classes.addToBasketBtn}>
                                        +
                                    </Button>
                                </div>
                                <div className={classes.priceContainer}>
                                    <div className={classes.productPrice}>
                                        <h5>1,700,000</h5>
                                    </div>
                                    <div className={classes.productPriceAfter}>
                                        <h5>تومان</h5>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </SwiperSlide>
                {/*---------- Slide -------- */}
                <SwiperSlide className={classes.slide}>
                    <div className={classes.product}>
                        <a href="#" className={classes.productImage}>
                            <img src="/novinMobl/images/product-images/novinMobl-img (8).jpg" />
                        </a>
                        <div className={classes.productDetails}>
                            <div className={classes.productTitle}>
                                <h5>میز پذیرایی شیشه</h5>
                            </div>
                            <div className={classes.productSubtitle}>
                                <h5>طرح گرد</h5>
                            </div>
                            <div className={classes.productPriceContainer}>
                                <div className={classes.addToBasket}>
                                    <Button variant="contained" color="btnThree" className={classes.addToBasketBtn}>
                                        +
                                    </Button>
                                </div>
                                <div className={classes.priceContainer}>
                                    <div className={classes.productPrice}>
                                        <h5>900,000</h5>
                                    </div>
                                    <div className={classes.productPriceAfter}>
                                        <h5>تومان</h5>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </SwiperSlide>
                {/*---------- Slide -------- */}
                <SwiperSlide className={classes.slide}>
                    <div className={classes.product}>
                        <a href="#" className={classes.productImage}>
                            <img src="/novinMobl/images/product-images/novinMobl-img (37).jpg" />
                        </a>
                        <div className={classes.productDetails}>
                            <div className={classes.productTitle}>
                                <h5>کشو اداری سه طبقه</h5>
                            </div>
                            <div className={classes.productSubtitle}>
                                <h5>مشکی مدل h-4</h5>
                            </div>
                            <div className={classes.productPriceContainer}>
                                <div className={classes.addToBasket}>
                                    <Button variant="contained" color="btnThree" className={classes.addToBasketBtn}>
                                        +
                                    </Button>
                                </div>
                                <div className={classes.priceContainer}>
                                    <div className={classes.productPrice}>
                                        <h5>800,000</h5>
                                    </div>
                                    <div className={classes.productPriceAfter}>
                                        <h5>تومان</h5>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </SwiperSlide>
                {/*---------- Slide -------- */}
                <SwiperSlide className={classes.slide}>
                    <div className={classes.product}>
                        <a href="#" className={classes.productImage}>
                            <img src="/novinMobl/images/product-images/novinMobl-img (29).jpg" />
                        </a>
                        <div className={classes.productDetails}>
                            <div className={classes.productTitle}>
                                <h5>صندلی ساده پذیرایی</h5>
                            </div>
                            <div className={classes.productSubtitle}>
                                <h5>مدل 13-u</h5>
                            </div>
                            <div className={classes.productPriceContainer}>
                                <div className={classes.addToBasket}>
                                    <Button variant="contained" color="btnThree" className={classes.addToBasketBtn}>
                                        +
                                    </Button>
                                </div>
                                <div className={classes.priceContainer}>
                                    <div className={classes.productPrice}>
                                        <h5>450,000</h5>
                                    </div>
                                    <div className={classes.productPriceAfter}>
                                        <h5>تومان</h5>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </SwiperSlide>
                {/*---------- Slide -------- */}
                <SwiperSlide className={classes.slide}>
                    <div className={classes.product}>
                        <a href="#" className={classes.productImage}>
                            <img src="/novinMobl/images/product-images/novinMobl-img (14).jpg" />
                        </a>
                        <div className={classes.productDetails}>
                            <div className={classes.productTitle}>
                                <h5>میز پذیرایی مدل ونوس</h5>
                            </div>
                            <div className={classes.productSubtitle}>
                                <h5>چوب تیره مدل p-12</h5>
                            </div>
                            <div className={classes.productPriceContainer}>
                                <div className={classes.addToBasket}>
                                    <Button variant="contained" color="btnThree" className={classes.addToBasketBtn}>
                                        +
                                    </Button>
                                </div>
                                <div className={classes.priceContainer}>
                                    <div className={classes.productPrice}>
                                        <h5>1,500,000</h5>
                                    </div>
                                    <div className={classes.productPriceAfter}>
                                        <h5>تومان</h5>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </SwiperSlide>
                {/*---------- Slide -------- */}
                <SwiperSlide className={classes.slide}>
                    <div className={classes.product}>
                        <a href="#" className={classes.productImage}>
                            <img src="/novinMobl/images/product-images/novinMobl-img (13).jpg" />
                        </a>
                        <div className={classes.productDetails}>
                            <div className={classes.productTitle}>
                                <h5>مبل راحتی </h5>
                            </div>
                            <div className={classes.productSubtitle}>
                                <h5>4 نفره مدل b-5</h5>
                            </div>
                            <div className={classes.productPriceContainer}>
                                <div className={classes.addToBasket}>
                                    <Button variant="contained" color="btnThree" className={classes.addToBasketBtn}>
                                        +
                                    </Button>
                                </div>
                                <div className={classes.priceContainer}>
                                    <div className={classes.productPrice}>
                                        <h5>3,500,000</h5>
                                    </div>
                                    <div className={classes.productPriceAfter}>
                                        <h5>تومان</h5>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </SwiperSlide>
                {/*---------- Slide -------- */}
                <SwiperSlide className={classes.slide}>
                    <div className={classes.product}>
                        <a href="#" className={classes.productImage}>
                            <img src="/novinMobl/images/product-images/novinMobl-img (7).jpg" />
                        </a>
                        <div className={classes.productDetails}>
                            <div className={classes.productTitle}>
                                <h5>نیمکت چوب ام دی اف</h5>
                            </div>
                            <div className={classes.productSubtitle}>
                                <h5>سفید مدل n-43</h5>
                            </div>
                            <div className={classes.productPriceContainer}>
                                <div className={classes.addToBasket}>
                                    <Button variant="contained" color="btnThree" className={classes.addToBasketBtn}>
                                        +
                                    </Button>
                                </div>
                                <div className={classes.priceContainer}>
                                    <div className={classes.productPrice}>
                                        <h5>500,000</h5>
                                    </div>
                                    <div className={classes.productPriceAfter}>
                                        <h5>تومان</h5>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </SwiperSlide>
            </Swiper>
        </div>
    )
}


export default ProductLIstOne
