import * as React from 'react';
import { styled, useTheme ,alpha} from '@mui/material/styles';

import { Link } from 'react-router-dom';

import MuiAppBar from '@mui/material/AppBar';
import MenuIcon from '@mui/icons-material/Menu';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import InboxIcon from '@mui/icons-material/MoveToInbox';
import MailIcon from '@mui/icons-material/Mail';
import SearchIcon from '@mui/icons-material/Search';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import WeekendIcon from '@mui/icons-material/Weekend';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';

import { Box , Drawer, CssBaseline, Toolbar, List, Typography, Divider, IconButton, 
    ListItem, ListItemIcon, ListItemText, InputBase } from '@mui/material';

import { makeStyles } from '@mui/styles';
import NavBarTabs from './Navbar/NavbarTabs';


const useStyles = makeStyles((theme) =>({

    siteTitle:{
      fontSize: "16px",
      '& a':{
        color:  theme.palette.btnThree.main,
        
        }
    },
    desctopHeaderSearch:{
       flex: 1,
    },
    ToolbarOne:{
      maxWidth: "1300px",
      width: "100%",
      margin: "0 auto",
      minHeight: "51px!important",
    },
    ToolbarTwo:{
      maxWidth: "1300px",
      width: "100%",
      margin: "0 auto",
      minHeight: "49px!important",
    },


    // @medya
    [theme.breakpoints.up("md")]:{
        openDrawer:{
            display: "none!important"
        },
        mobileSearchIcon:{
          display: "none!important",
        },
    },
    [theme.breakpoints.down("md")]:{
      desctopHeaderSearch:{
            display: "none!important"
        },
        siteTitle:{
          flex: 1,
      },
      ToolbarTwo:{
        display: "none!important",
      },
    }

}))



const drawerWidth = 240;

// -------- main content responsive with sidebar
// const Main = styled('main', { shouldForwardProp: (prop) => prop !== 'open' })(
//   ({ theme, open }) => ({
//     flexGrow: 1,
//     padding: theme.spacing(3),
//     transition: theme.transitions.create('margin', {
//       easing: theme.transitions.easing.sharp,
//       duration: theme.transitions.duration.leavingScreen,
//     }),
//     marginLeft: `-${drawerWidth}px`,
//     ...(open && {
//       transition: theme.transitions.create('margin', {
//         easing: theme.transitions.easing.easeOut,
//         duration: theme.transitions.duration.enteringScreen,
//       }),
//       marginRight: 0,
//     }),
//   }),
// );

const AppBar = styled(MuiAppBar, {
  shouldForwardProp: (prop) => prop !== 'open',
})(({ theme, open }) => ({
  transition: theme.transitions.create(['margin', 'width'], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
//   -----App bar background
   background: theme.palette.primary.whiteBG,
  ...(open && {
    width: `calc(100% - ${drawerWidth}px)`,
    marginRight: `${drawerWidth}px`,
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  }),
}));

const DrawerHeader = styled('div')(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  padding: theme.spacing(0, 1),
  // necessary for content to be below app bar
  ...theme.mixins.toolbar,
  justifyContent: 'flex-end',
}));





export default function NavBar() {
  const theme = useTheme();
  const [open, setOpen] = React.useState(false);
  const classes = useStyles();
  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  return (

    <Box sx={{ display: 'flex' }}>
      <CssBaseline />
      <AppBar position="fixed" open={open}>
        <Toolbar className={classes.ToolbarOne}>
          <IconButton
            className={classes.openDrawer}
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            edge="start"
            sx={{ ml: 3,  ...(open && { display: 'none' }) }}
          >
            <MenuIcon />
          </IconButton>
          <Typography className={classes.siteTitle} variant="h6" noWrap component="div">
            <Link to="/novinMobl">
              نوین مبل
            </Link>
          </Typography>
          <Search className={classes.desctopHeaderSearch}>
            <SearchIconWrapper>
              <SearchIcon />
            </SearchIconWrapper>
            <StyledInputBase
              placeholder="جست و جو"
              inputProps={{ 'aria-label': 'search' }}
            />
          </Search>
          <IconButton className={classes.mobileSearchIcon}>
            <SearchIcon/>
          </IconButton>
          <IconButton component={Link} to="/ShopingCart">
            <ShoppingCartIcon/>
          </IconButton>
          <IconButton>
            <AccountCircleIcon/>
          </IconButton>
        </Toolbar>
        {/* Toolbar Two */}
        <Toolbar className={classes.ToolbarTwo}>
            <NavBarTabs/>
        </Toolbar>
      </AppBar>
    {/* ----------sidebar---------- */}
      <Drawer
        sx={{
          width: drawerWidth,
          flexShrink: 0,
          '& .MuiDrawer-paper': {
            width: drawerWidth,
            boxSizing: 'border-box',
          },
        }}
        variant="persistent"
        anchor="right"
        open={open}
      >
        <DrawerHeader>
          <IconButton onClick={handleDrawerClose}>
            {theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
          </IconButton>
        </DrawerHeader>
        <Divider />
        <List>
          {/* LIstItem */}
          <ListItem button component={Link} to="/category">
            <ListItemIcon>
              <WeekendIcon/>
            </ListItemIcon>
            <ListItemText>
              مبلمان اداری
            </ListItemText>
          </ListItem>
          {/* LIstItem */}
          <ListItem button component={Link} to="/category">
            <ListItemIcon>
              <WeekendIcon/>
            </ListItemIcon>
            <ListItemText>
              مبلمان منزل
            </ListItemText>
          </ListItem>
          {/* LIstItem */}
          <ListItem button component={Link} to="/category">
            <ListItemIcon>
              <WeekendIcon/>
            </ListItemIcon>
            <ListItemText>
              مبل
            </ListItemText>
          </ListItem>
          {/* LIstItem */}
          <ListItem button component={Link} to="/category">
            <ListItemIcon>
              <WeekendIcon/>
            </ListItemIcon>
            <ListItemText>
              صندلی
            </ListItemText>
          </ListItem>
          {/* LIstItem */}
          <ListItem button component={Link} to="/category">
            <ListItemIcon>
              <WeekendIcon/>
            </ListItemIcon>
            <ListItemText>
              میز
            </ListItemText>
          </ListItem>
          {/* LIstItem */}
          <ListItem button component={Link} to="/category">
            <ListItemIcon>
              <WeekendIcon/>
            </ListItemIcon>
            <ListItemText>
              کتابخانه و کمد
            </ListItemText>
          </ListItem>
        </List>
        <Divider />

      </Drawer>
    </Box>
  );
}


// -------- search
const Search = styled('div')(({ theme }) => ({
  position: 'relative',
  borderRadius: theme.shape.borderRadius,
  backgroundColor: alpha(theme.palette.common.black, 0.15),
  '&:hover': {
    backgroundColor: alpha(theme.palette.common.black, 0.25),
  },
  marginRight: theme.spacing(2),
  marginLeft: 0,
  width: '100%',
  [theme.breakpoints.up('sm')]: {
    marginLeft: theme.spacing(3),
    width: '600px',
  },
}));
// --
const SearchIconWrapper = styled('div')(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: '100%',
  position: 'absolute',
  pointerEvents: 'none',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}));

// --
const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: 'inherit',
  '& .MuiInputBase-input': {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingRight: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('md')]: {
      width: '600px',
    },
  },
}));