import { Button } from '@mui/material';
import { makeStyles } from '@mui/styles'
import React from 'react'

import { Link } from 'react-router-dom';

const useStyles = makeStyles((theme)=>({
    mainContainer:{
        marginTop: "10px",
        display: "flex",
        flexWrap: "wrap",
        justifyContent: "center"
    },
    product:{
        margin: "5px",
        height: "340px",
        width: "235px",
        background: "#fff",
        border: '1px solid lightgray',
        borderRadius: "8px",
        display: "flex",
        flexDirection: "column",
        
    },
    
    productImage:{
        width: "100%",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        overflow: "hidden",
        '& img':{
            padding: "5px",
            width: "95%",
            objectFit: "contain",
        }
        
    },
    productDetails:{
        margin: "5px",
    },
    productTitle:{
        margin: "5px",
        '& h5':{
            fontSize: "15x"
        }
    },
    productSubtitle:{
        marginRight: "5px",
        marginTop: "3px",
        '& h5':{
            fontSize: "12px"
        }
    },
    productPriceContainer:{
        
        display: "flex",
        margin: "10px 5px",
        alignItems: "center",
        justifyContent: "space-between",
    },
    addToBasket:{
        
    },
    addToBasketBtn:{
        color: "#fff!important",
        fontSize: "14px!important",
        maxWidth: "5px!important",
        padding: "2px!important",
        minWidth: "40px!important"
    },
    priceContainer:{
        display:"flex",
        alignItems: "center"
    },
    productPrice:{
        "& h5":{
            fontSize: "16px",
        }
    },
    productPriceAfter:{
        marginTop: "3px",
        paddingRight: "5px",
        '& h5':{
            color: "white",
            fontWeight: "normal",
            background: theme.palette.btnThree.main,
            fontSize: "11px",
            padding: "3px",
            borderRadius: "5px"
        }
    },
    //  @media
    [theme.breakpoints.down("md")]:{
        product:{
            margin: "2px",
            height: "300px",
            width: "190px",
            background: "#fff",
            border: '1px solid lightgray',
            borderRadius: "8px",
            display: "flex",
        },
    },
    [theme.breakpoints.down("sm")]:{
        mainContainer:{
            marginTop: "4px",
            display: "flex",
            flexWrap: "wrap",
        },
        product:{
            margin: "5px",
            height: "250px",
            width: "150px",
            border: '1px solid lightgray',
            borderRadius: "8px",
        },
        productPrice:{
            "& h5":{
                fontSize: "13px",
            }
        },
        productPriceAfter:{
            marginTop: "3px",
            paddingRight: "5px",
            '& h5':{
                color: "white",
                fontWeight: "normal",
                background: theme.palette.btnThree.main,
                fontSize: "10px",
                padding: "3px",
                borderRadius: "5px"
            }
        },
        addToBasketBtn:{
            fontSize: "12px!important",
            maxWidth: "5px!important",
            padding: "2px!important",
            minWidth: "30px!important"
        },

    }
}))

