import { makeStyles } from '@mui/styles'
import React from 'react'
import CategoryList from '../components/category/CategoryList';
import ListC from '../components/category/ListC';

const useStyles = makeStyles((theme)=>({
    CategoryPage:{
        marginTop: "110px",
        maxWidth: "1300px",
        padding: "5px 10px",
        margin: "0 auto",
    },
    ListContainer:{
        background: "#fff",
        padding: "10px",
        borderRadius: "8px"
    },
    ListTitle:{
        marginRight: "10px",
        paddingTop: "8px",
        paddingBottom: "15px",
        borderBottom: "1px solid lightgray",
        '& h2':{
            fontSize: "18px"
        }
    },
    categoryList:{
        height: "auto",
        padding: "10px",
    },



    // @media
    [theme.breakpoints.down("md")]:{
        CategoryPage:{
            marginTop:"51px",
        },
    },
    [theme.breakpoints.down("sm")]:{
        CategoryPage:{
            marginTop:"51px",
            maxWidth: "1300px",
            padding: "3px 3px",
            margin: "0 auto",
        },
        ListContainer:{
            padding: "3px",
            borderRadius: "8px"
        },
    }
}))

function Category() {
    const classes = useStyles();
    return (
        <div className={classes.CategoryPage}>
            <div className={classes.ListContainer}>
                <div className={classes.ListTitle}>
                    <h2>مبلمان اداری</h2>
                </div>
                <div className={classes.categoryList}>
                    <ListC/>
                </div>
            </div>
        </div>
    )
}

export default Category
